import Graph from 'graphology';
import gexf from 'graphology-gexf';
import graphml from 'graphology-graphml';
import straighten from '../../lib/straighten';
function importExample(options, callback) {
    var url = NANSI_BASE_URL + "resources/" + options.name + ".gexf";
    fetch(url)
        .then(function (response) { return response.text(); })
        .then(function (text) {
        var graph = gexf.parse(Graph, text);
        callback(null, graph);
    });
}
function importText(options, callback) {
    if (options.format === 'gexf')
        return callback(null, gexf.parse(Graph, options.text));
    if (options.format === 'graphml')
        return callback(null, graphml.parse(Graph, options.text));
    return callback(new Error('nansi/app/lib/import: unknown text format!'));
}
function importFile(options, callback) {
    var reader = new FileReader();
    reader.onload = function (e) {
        var text = e.target.result;
        var name = options.file.name;
        var format = null;
        if (name.endsWith('.gexf'))
            format = 'gexf';
        else if (name.endsWith('.graphml'))
            format = 'graphml';
        if (!format)
            return callback(new Error('unknown format'));
        return importText({ type: 'text', text: text, format: format }, callback);
    };
    reader.readAsText(options.file);
}
var importFunctions = {
    example: importExample,
    text: importText,
    file: importFile
};
export function importGraph(options, callback) {
    var fn = importFunctions[options.type];
    if (!fn)
        throw new Error('nansi/app/lib/import: unknown import type!');
    fn(options, function (err, graph) {
        if (err)
            return callback(err);
        // Common graph processing
        var model = straighten(graph);
        return callback(null, { graph: graph, model: model });
    });
}
